@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Mono&family=Pacifico&display=swap');

.testimonials{
    position: relative;
    height: 100vh;
    width: 100%;
    background: linear-gradient(
        to top left,
        #1e364c 0%,
        black 100%
      );
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.testimonials-header{
    font-family: 'IBM Plex Mono', monospace;
    font-size: xx-large;
    position: absolute;
    top:50px;
    left:15vw;
    height:106px;
    color: #57697B;
}

.image-slider-container {
    max-width: 1000px;
    max-height: 100%;
    margin: 10%;
    margin-top: 14%;
  }
  
  .slider-item {
    .card {
      border-radius: 10px;
      border: 0;
      box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
      padding: 20px;
      text-align: center;
      height: 60vh;
      margin: 0 6px;
      width:18vw;
      transition: transform 0.3s ease-in-out;
      background-color: #e2c5505f;
  
      h3 {
        font-size: 1.5rem;
        margin-bottom: 10px;
        font-family: 'IBM Plex Mono', monospace;
      }
  
      p {
        font-size: 1rem;
      }
  
      .rating {
        margin-top: 10px;
      }
    }
  }
  



  .testers{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    z-index: 5;
    width: 100%;
    height: 400px;
    transition: transform 0.3s ease-in-out;
    
  }

  .testers:hover{
    transform: translateY(-60px);
  }
  .testers:hover .test-comment{
    display: block;
  }

  .tester-image{
    border: 1px solid black;
    border-radius: 50%;
    height: 200px;
    width:200px ;
    margin-bottom: 10%;
  }

  .tester-details{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    z-index: 5;
    width: 100%;
    height: 100%;
    margin-top: 20px;
  }

  .test-comment{
    display: none;
  }
